import { Grid,Typography,Link,Tooltip} from '@material-ui/core';
import Zoom from '@material-ui/core/Zoom';
import React from 'react';
import { useFooterStyles } from './Styles/styles';
import {socialMedia} from './Datas/getData';
import {Favorite, IndeterminateCheckBoxOutlined} from '@material-ui/icons';


import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import {WhatsApp,LinkedIn,GitHub,Mail, Phone} from '@material-ui/icons';

import Paper from '@material-ui/core/Paper';
import Grow from '@material-ui/core/Grow';


function Footer(props) {
        const [activeStep, setActiveStep] = React.useState(-1);
        const classes = useFooterStyles();   
        
        const handleSteps = step => {
            setActiveStep(step);
          };
        
          const stepIcon=(i,socialMedia)=>{
            return(
                <Link onClick={()=>handleSteps(i)}>
                    {socialMedia}
                </Link>                
            )
          }
          
        return (           
            <div className={classes.footerContainer} id="CONTACT">             
                <Grid container justifyContent="center" alignItems='center' spacing={3}>                   
                        <Stepper activeStep={activeStep} connector={false}>
                            {socialMedia.map((contact,index) => {
                            return (
                                <Step key={index}>              
                                <StepLabel StepIconComponent={()=>stepIcon(index,contact.socialMedia)} >                
                                    {(index===activeStep) && 
                                        <Grow in={true} >
                                            <Paper elevation={4}>
                                              <Link href={contact.link ? contact.link : null}
                                                    underline={contact.link?'hover':'none'}> 
                                                    {contact.iconData}
                                              </Link>
                                            </Paper>
                                        </Grow>
                                    }
                                </StepLabel>
                                </Step>
                            );
                            })}
                        </Stepper>
                </Grid>
                <Grid container justifyContent="center" alignItems='center' spacing={3}>
                        <Grid item>
                            <Typography variant="body2" align='center' color='textPrimary'>
                                    Made with <Favorite fontSize="small" className={classes.heart} /> in India
                            </Typography>
                        </Grid>
                </Grid>
            </div>
        );
}
export default Footer;